<template>
  <div> 
    <p v-if="totalWithoutDiscount" class="strike-through"> 
      {{ formatPrice(totalWithoutDiscount) }} ({{formatPrice(price)}} x {{count}} tickets)
    </p>
    <p>
      <strong>
        {{ formatPrice(total) }} 
      </strong>
      <span> 
        ({{formatPrice(singlePriceWithDiscount)}} x {{count}} tickets) 
      </span>
    </p>
  </div>
</template>

<script>

export default {
  name: "TicketTotal",
  components: {
  },
  props: {
    count: {
      type: Number,
      required: true
    },
    price: {
      type: Number,
      required: true
    },
    discountPercent: {
      type: Number,
      required: true
    }
  },
  computed: {
    singlePriceWithDiscount(){
      const { price, discountPercent } = this
      return discountPercent
        ? (100 - discountPercent) / 100.0 * price
        : price
    },
    totalWithoutDiscount() {
      const {count, price, discountPercent} = this
      if (!discountPercent) return null
      return count * price
    },
    total() {
      const total = this.count * this.price
      return this.discountPercent
        ? (100 - this.discountPercent) / 100.0 * total
        : total
    },
  },
  methods: {
    formatPrice(price){
      return `$${ Math.round(price) / 100}`
    }
  }
};
</script>

<style scoped>
.strike-through {
  text-decoration: line-through;
}
</style>
