<template>
  <section class="container">
    <div class="columns is-centered">
      <div class="column is-6">
        <form @submit.prevent="validateAndEmit">
          <h2 class="title is-3">Your Information</h2>
          <PersonInfo class="section-spacing" :person="person" :$v="$v" />
          <hr style="margin-top:  3rem">
          <!-- <h2 class="title is-3">General Questions</h2> -->
          <TicketQuestions
            class="section-spacing"
            :questions="ticketQuestions"
            :$v="$v"
          />
          <hr style="margin-top:  3rem">
          <ApplicationQuestions
            class="section-spacing"
            v-model="applicationQuestions"
            :validations="$v.applicationQuestions"
          />
          <!-- <h2 class="title is-3">Questions for Ticket</h2> -->

          <div style="margin-top:  3rem" class="buttons has-addons is-right">
            <input
              class="button is-primary is-medium"
              type="submit"
              :value="submitText"
            />
          </div>
        </form>
      </div>
    </div>
  </section>
</template>

<script>
import NProgress from "nprogress";
import PersonInfo from "@/ticketing/components/PersonInfo";
import ApplicationQuestions from "@/ticketing/components/ApplicationQuestions";
import TicketQuestions from "@/ticketing/components/TicketQuestions.vue";
import {
  personQuestionsValidations,
  applicationQuestionsValidations,
  ticketQuestionsValidations
} from "@/mixins/questionsMixin";
import { mapGetters } from "vuex";

export default {
  name: "TicketOfferForm",
  components: {
    PersonInfo,
    ApplicationQuestions,
    TicketQuestions
  },
  props: {
    submitText: {
      type: String,
      default: "Submit"
    }
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    }),
    applicationQuestions() {
      return this.person.data;
    },
    ticketQuestions() {
      return this.person.data;
    },
  },
  validations() {
    // if (this.ticketQuestions.foundedYesNo === "true") {
    //   return {
    //     person: personQuestionsValidations,
    //     applicationQuestions: applicationQuestionsValidations,
    //     ticketQuestions: ticketQuestionsValidations[1]
    //   };
    // } else {
    return {
      person: personQuestionsValidations,
      applicationQuestions: applicationQuestionsValidations,
      ticketQuestions: ticketQuestionsValidations[0]
    };
    // }
  },
  methods: {
    validateAndEmit() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        this.$emit("submitForm");
      }
    }
  }
};
</script>
