import { required, integer, email, maxLength } from "vuelidate/lib/validators";

export const personQuestionsMixin = {
  data() {
    return {
      person: {
        id: this.personInit ? this.personInit.id : "",
        firstName: this.personInit ? this.personInit.firstName : "",
        lastName: this.personInit ? this.personInit.lastName : "",
        email: this.personInit ? this.personInit.email : "",
        data: this.personInit ? this.personInit.data : {}
      }
    };
  }
};

export const personQuestionsValidations = {
  firstName: { required },
  lastName: { required },
  email: { required, email }
};

export const applicationQuestionsMixin = {
  data() {
    return {
      applicationQuestions: {
        yearOfBirth: this.personInit ? this.personInit.data.yearOfBirth : "",
        gender: this.personInit ? this.personInit.data.gender : "",
        eventsAttended:
          this.personInit &&
          this.personInit.data &&
          this.personInit.data.eventsAttended
            ? this.personInit.data.eventsAttended
            : [],
        occupation: this.personInit ? this.personInit.data.occupation : "",
        organisation: this.personInit ? this.personInit.data.organisation : "",
        industry: this.personInit ? this.personInit.data.industry : "",
        // educationLevel: this.personInit
        //   ? this.personInit.data.educationLevel
        //   : ""
      }
    };
  }
};

export const applicationQuestionsValidations = {
  yearOfBirth: { required, integer },
  gender: { required },
  eventsAttendedBool: {},
  occupation: { required },
  //organisation: { required },
  //industry: { required },
  //educationLevel: { required }
};

